.footer--container{
  position: relative;
}
.footer--container>hr{
  border: 1px solid lightgrey;
}
.footer{
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  gap: 4rem;
  align-items: center;
  justify-content: center;
  height: 20rem;
}
.social--link{
  display: flex;
  flex-direction: row;
  gap: 4rem;
}
.social--link>img{
  width: 2rem;
  height: 2rem;
  cursor: pointer;
}
.logo--footer>img{
  width: 10rem;
  justify-content: center;
  align-items: center;
}
.footer--blur--1{
  bottom: 0rem;
  right: 15%;
  width: 26rem;
  height: 12rem;
  filter: blur(200px);
  background-color:red;

}
.footer--blur--2{

  bottom: 0rem;
  left: 15%;
  width: 26rem;
  height: 12rem;
  filter: blur(200px);
  background-color:rgb(211, 129, 21);
}