.hero {
  display: flex;
  justify-content: space-between;
}
.hero--left {
  padding: 2rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  flex: 3;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.the--best--fitness {
  margin-top: 4rem;
  background-color: #363d42;
  border-radius: 4rem;
  width: fit-content;
  padding: 20px 13px;
  text-transform: uppercase;
  color: white;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.the--best--fitness > span {
  z-index: 2;
  letter-spacing: 0.2rem;
}
.the--best--fitness > div {
  position: absolute;
  background-color: var(--ORANGE);
  width: 5.4rem;
  height: 80%;
  border-radius: 3rem;
  left: 8px;
  z-index: 1;
}
.hero--text {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  text-transform: uppercase;
  color: white;
  font-size: 4.5rem;
  font-weight: bold;
  text-overflow: inherit;
}
.hero--text > div:nth-of-type(3) {
  font-size: 1rem;
  font-weight: 200;
  text-transform: none;
  letter-spacing: 1px;
  width: 80%;
}
.figures {
  display: flex;
  flex-direction: row;
  gap: 6rem;
}
.figures > div {
  display: flex;
  flex-direction: column;
}
.figures > div > span:nth-child(1) {
  color: white;
  font-size: 2rem;
}
.figures > div > span:nth-child(2) {
  color: var(--gray);
  font-size: 1.2rem;
  text-transform: uppercase;
}
.hero--button {
  display: flex;
  gap: 1rem;
  font-weight: normal;
}
.hero--button > :nth-child(1) {
  color: white;
  background-color: var(--ORANGE);
  width: 12rem;
}
.hero--button > :nth-child(2) {
  color: white;
  background-color: transparent;
  width: 12rem;
  border: 2px solid var(--ORANGE);
}
.btn {
  background-color: white;
  padding: 0.5rem;
  font-weight: bold;
  border: 4px solid transparent;
  transition: 300ms all;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
}
.btn:hover {
  cursor: pointer;
}
.hero--right {
  flex: 1;
  background-color: var(--ORANGE);
  position: relative;
}
.hero--right > .btn {
  position: absolute;
  right: 3rem;
  top: 2rem;
  color: black;
  width: 10rem;
  border-radius: 5px;
}
.heart--rate {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: fit-content;
  background-color: var(--darkgray);
  position: absolute;
  padding: 2rem;
  align-items: start;
  border-radius: 5px;
  right: 8rem;
  top: 7rem;
}
.heart--rate > img {
  width: 2rem;
}
.heart--rate > img:hover {
  width: 3rem;
  transition: 300ms;
}
.heart--rate > :nth-child(2) {
  color: var(--gray);
}
.heart--rate > :nth-child(3) {
  color: white;
  font-size: 1.5rem;
}
.hero--image {
  position: absolute;
  right: 16rem;
  top: 10rem;
  width: 23rem;
}
.hero--image--back {
  position: absolute;
  top: 6rem;
  right: 24rem;
  z-index: -1;
  width: 19rem;
}
.calories {
  display: flex;
  gap: 2rem;
  background-color: var(--caloryCard);
  border-radius: 5px;
  padding: 1rem;
  width: fit-content;
  position: absolute;
  top: 36rem;
  right: 46rem;
}
.calories > img {
  width: 3rem;
}
.calories > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.calories > div > :nth-child(1) {
  color: var(--gray);
}
.calories > div > :nth-child(2) {
  color: white;
  font-size: 1.5rem;
}
.btn:hover {
  background-color: #f7770f;
  border: 4px solid white;
}
.hero--blure {
  width: 22rem;
  height: 30rem;
  left: 0;
}
@media and (mix-width: 768px) {
  .hero {
    flex-direction: column;
  }
  .hero--blure {
    width: 14rem;
  }
  .the--best--fitness {
    margin-top: 0;
    font-size: small;
    align-self: center;
    transform: scale(0.8);
  }
  .hero--text {
    font-size: xx-large;
    align-items: center;
    justify-content: center;
  }
  .hero--text > div:nth-of-type(3) {
    font-size: small;
    font-weight: 200;
    letter-spacing: 1px;
    text-align: center;
  }
  .hero--button {
    justify-content: center;
  }
  .figures > div > span:nth-of-type(1) {
    font-size: large;
  }
  .figures > div > span:nth-of-type(2) {
    font-size: small;
  }
  .hero--right {
    position: relative;
    background: none;
  }
  .heart--rate {
    left: 1rem;
    top: 2rem;
  }
  .calories {
    position: relative;
    top: 5rem;
    left: 2rem;
  }
  .calories > img {
    width: 2rem;
  }
  .calories > div > :nth-child(2) {
    color: white;
    font-size: 1rem;
  }
  .hero--image {
    position: relative;
    width: 15rem;
    left: 7rem;
    top: 4rem;
    align-self: center;
  }
  .hero--image--back {
    width: 15rem;
    left: 2rem;
    top: 0rem;
  }
}
